import { Component, OnInit } from '@angular/core';

import { EmployeeService } from '../shared/employee.service'
import { Employee } from '../shared/employee.model';
import { ToastrService } from 'ngx-toastr';    
import { NgForm } from '@angular/forms';

import { NgbModal, ModalDismissReasons } from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'app-employee-list',
  templateUrl: './employee-list.component.html',
  styleUrls: ['./employee-list.component.css']
})
export class EmployeeListComponent implements OnInit {
  closeResult: string;

  constructor(private employeeService: EmployeeService,private toastr : ToastrService, private modalService: NgbModal) { }
  
  ngOnInit() {
    this.employeeService.getEmployeeList();
    this.resetForm();
  }

  resetForm(form?: NgForm) {
    if (form != null)
      form.reset();
    this.employeeService.selectedEmployee = {
      id: null,
      firstName: '',
      lastName: '',
      mobileNumber: '',
      email: '',

    }
  }

  showForEdit(emp: Employee, content) {

    this.employeeService.selectedEmployee = Object.assign({}, emp);

  }
   
 ver(content) {
  this.modalService.open(content).result.then((result) => {
    this.closeResult = `Closed with: ${result}`;
  }, (reason) => {
    this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
    
  });
 }

  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return  `with: ${reason}`;
    }
  }
 
  onSubmit(form: NgForm, content) {
    if (form.value.id == null) {
      this.employeeService.postEmployee(form.value)
        .subscribe(data => {
          this.resetForm(form);
          this.employeeService.getEmployeeList();
          this.toastr.success('Nuevo registro agregado con éxito!',' Registro de empleados');
        })
    }
    else {
      this.employeeService.putEmployee(form.value.id, form.value)
      .subscribe(data => {
        this.resetForm(form);
        this.employeeService.getEmployeeList();
        this.toastr.info('Registro actualizado con éxito! ',' Registro de empleados');
      });
    }
  }

  onDelete(id: number) {
    if (confirm('Esta seguro de Eliminar este registro ?') == true) {
      this.employeeService.deleteEmployee(id)
      .subscribe(x => {
        this.employeeService.getEmployeeList();
        this.toastr.warning("Eliminado con éxito "," Registro de empleados");
      })
    }
  }
}





